@import url(https://fonts.googleapis.com/css?family=Roboto:300);
.Header {
  align-items: center;
  background-image: linear-gradient(to right, transparent, silver, silver, transparent);
  display: flex;
  font-family: "Roboto", sans-serif;
  font-size: 1.8rem;
  justify-content: center;
}

.todd-img {
  height: 60px;
  position: relative;
  top: 10px;
}

@media screen and (max-width: 760px) {
  .Header {
    font-size: 1.2rem;
  }

  .todd-img {
    height: 60px;
    position: relative;
    top: 12px;
  }
}

@media screen and (max-width: 500px) {
  .Header {
    color: rgb(238, 238, 238);
    font-size: 0.8rem;
  }

  .todd-img {
    height: 40px;
    position: relative;
    top: 8px;
  }
}

.bonus {
	align-items: center;
	background: rgb(50, 50, 50);
	display: flex;
	min-height: 2rem;
	margin: 0.5rem;
	padding: 1rem 1rem 1rem 0.5rem;
	position: relative;
	text-align: left;
}

.description {
	font-family: 'Roboto', sans-serif;
	margin: 0;
	word-wrap: break-word;
}

.delete-btn {
	cursor: pointer;
	font-size: 18px;
	font-weight: 100;
	margin: 0;
	padding: 2px;
	position: absolute;
	right: 0;
	top: 0;
	transition: all 0.5s ease-out;
}

.delete-btn:hover {
	background: white;
	color: rgb(50, 50, 50);
	transition: all 0.5s ease-out;
}

.PlayerForm {
  align-items: center;
  background: silver;
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
  opacity: 0.9;
  text-align: center;
  width: 70vw;
}

.player-drop-down,
.bonus-drop-down {
  font-size: 22px;
}

.bonus-input {
  font-size: 22px;
  width: 400px;
}

.player-btn {
  background: rgb(84, 84, 84);
  border: none;
  color: white;
  cursor: pointer;
  font-size: 22px;
  margin: 20px;
  min-height: 50px;
  padding: 10px 30px;
  transition: all 0.5s ease-out;
}

.player-btn:hover {
  background: rgb(242, 242, 242);
  color: rgb(84, 84, 84);
  transition: all 0.5s ease-out;
}

.player-btn:disabled {
  color: white;
  background: rgb(176, 176, 176);
  cursor: auto;
}

@media screen and (max-width: 1100px) {
  .PlayerForm {
    width: 90vw;
  }
}

@media screen and (max-width: 900px) {
  .PlayerForm {
    flex-direction: column;
    height: 300px;
    width: 70vw;
  }

  .bonus-input {
    width: auto;
  }
}


@media screen and (max-width: 500px) {
  .bonus-input {
    font-size: 16px;
    width: 200px;
  }
}
.player {
  height: 320px;
  margin: 10px;
  overflow: auto;
}

.team {
	cursor: pointer;
	transition: all 0.5s ease-out;
}

.team-controls {
	align-items: center;
	background: silver;
	display: flex;
	justify-content: space-evenly;
	margin: auto;
	width: 70vw;
}

.team:hover {
	opacity: 0.8;
	transition: all 0.3s ease-out;
}

.toggle-btn {
	background: rgb(84, 84, 84);
	border: 2px solid silver;
	color: white;
	cursor: pointer;
	padding: 10px 30px;
	transition: all 0.5s ease-out;
}

.toggle-btn:hover {
	background: rgb(242, 242, 242);
	color: rgb(84, 84, 84);
	transition: all 0.5s ease-out;
}

.search {
	height: 3rem;
}

.toggle-btn,
.search {
	font-size: 32px;
	margin: 1rem;
	width: 33%;
}

@media screen and (max-width: 1100px) {
	.team-controls {
		width: 90vw;
	}
}

@media screen and (max-width: 620px) {
	.team-controls {
		flex-direction: column;
	}

	.toggle-btn,
	.search {
		width: 80%;
	}
}

.MainPage {
	position: relative;
	text-align: center;
	opacity: 0.9;
}

.Teams,
.Players {
	background: silver;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin: 40px auto;
	width: 70vw;
}

.main-page-toggle-btn {
	background: rgb(84, 84, 84);
	border: 2px solid silver;
	color: white;
	cursor: pointer;
	font-size: 32px;
	margin: 2rem auto;
	padding: 1rem 3rem;
	transition: all 0.5s ease-out;
	width: 240px;
}

.main-page-toggle-btn:hover {
	background: rgb(242, 242, 242);
	color: rgb(84, 84, 84);
	transition: all 0.5s ease-out;
}

.players-wrapper,
.teams-wrapper {
	max-height: 55vh;
	overflow: auto;
}

.player,
.team {
	background: rgb(101, 101, 101);
	color: white;
	margin: 10px auto;
	width: 240px;
}

.red {
	background: rgb(108, 0, 0);
}

.green {
	background: rgb(0, 87, 0);
}

@media screen and (max-width: 1100px) {
	.Teams,
	.Players {
		width: 90vw;
	}
}

.overlay {
	-webkit-animation: overlay 0.4s ease-in-out forwards;
	        animation: overlay 0.4s ease-in-out forwards;
	background: rgb(218, 218, 218);
	height: 100vh;
	position: absolute;
	width: 100vw;
}

@-webkit-keyframes overlay {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 0.95;
	}
}

@keyframes overlay {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 0.95;
	}
}

.TeamModal {
	align-items: center;
	-webkit-animation: modal .6s ease-in-out forwards;
	        animation: modal .6s ease-in-out forwards;
	background: rgb(77, 77, 77);
	color: white;
	display: flex;
	height: 50vh;
	flex-direction: column;
	justify-content: space-evenly;
	left: 0;
	margin: 0 auto;
	position: absolute;
	right: 0;
	text-align: center;
	width: 40rem;
	z-index: 10;
}

@-webkit-keyframes modal {
	0% {
		opacity: 0;
		top: 100vh;
	}
	100% {
		opacity: 0.95;
		top: 25vh;
	}
}

@keyframes modal {
	0% {
		opacity: 0;
		top: 100vh;
	}
	100% {
		opacity: 0.95;
		top: 25vh;
	}
}

.redBg {
	background: linear-gradient(
		0deg,
		rgba(77, 77, 77, 1) 0%,
		rgba(168, 0, 0, 1) 100%
	);
}

.greenBg {
	background: linear-gradient(
		0deg,
		rgba(77, 77, 77, 1) 0%,
		rgba(0, 135, 10, 1) 100%
	);
}

.close-modal-btn {
	background: transparent;
	border: none;
	color: white;
	font-size: 36px;
	right: 0;
	padding: 10px 20px;
	position: absolute;
	top: 0;
	transition: all 0.5s ease-out;
}

.close-modal-btn:hover {
	background: silver;
	color: black;
	cursor: pointer;
}

.team-input {
	font-size: 22px;
}

.team-btn {
	background: rgb(31, 24, 24);
	border: none;
	color: white;
	cursor: pointer;
	font-size: 22px;
	margin: 20px;
	min-height: 50px;
	padding: 10px 30px;
	transition: all 0.5s ease-out;
}

.team-btn:hover {
	background: rgb(242, 242, 242);
	color: rgb(84, 84, 84);
	transition: all 0.5s ease-out;
}

.team-btn:disabled {
	color: white;
	background: rgb(176, 176, 176);
	cursor: auto;
}

.switch {
	display: inline-block;
	height: 34px;
	position: relative;
	width: 60px;
}

.switch input {
	opacity: 0;
	width: 0;
	height: 0;
}

.slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgb(0, 87, 0);
	transition: 0.4s;
}

.slider:before {
	position: absolute;
	content: '';
	height: 26px;
	width: 26px;
	left: 4px;
	bottom: 4px;
	background-color: white;
	transition: 0.4s;
}

input:checked + .slider {
	background-color: rgb(108, 0, 0);
}

input:focus + .slider {
	box-shadow: 0 0 1px rgb(108, 0, 0);
}

input:checked + .slider:before {
	transform: translateX(26px);
}

@media screen and (max-width: 1100px) {
	.TeamForm {
		width: 90vw;
	}
}

@media screen and (max-width: 800px) {
	.TeamForm {
		flex-direction: column;
		height: 300px;
		width: 70vw;
	}
}

@media screen and (max-width: 620px) {
	.TeamModal {
		height: 60vh;
		width: 100vw;
	}

	@-webkit-keyframes modal {
		0% {
			opacity: 0;
			top: 100vh;
		}
		100% {
			opacity: 0.95;
			top: 5vh;
		}
	}

	@keyframes modal {
		0% {
			opacity: 0;
			top: 100vh;
		}
		100% {
			opacity: 0.95;
			top: 5vh;
		}
	}
}

@media screen and (max-width: 500px) {
	.team-input {
		font-size: 26px;
		width: 100px;
	}
}

.Loading {
  align-items: center;
  display: flex;
  justify-content: center;
  height: 100%;
}

.loading-img {
  height: 140px;
}

.error {
	background: rgba(255, 0, 0, 0.434);
	border-radius: 5%;
	border: 4px solid white;
    color: white;
	margin: 5rem auto;
	opacity: 0.95;
	padding: 5%;
	text-align: center;
	width: 50%;
}

@media screen and (max-width: 760px) {
    .error {
        width: 70%
    }
}

.App {
  background: url(/long-shot-league-admin/static/media/court.58e6fc11.jpg) center no-repeat;
  background-attachment: fixed;
  background-size: cover;
  display: grid;
  grid-template-rows: 100px 1fr;
  min-height: 100vh;
  position: relative;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

